import React from "react";
import { graphql } from "gatsby";
import { RichText } from "prismic-reactjs";
import { get } from "lodash";

// Core
import Layout from "@landingpage/core/src/components/Layout";
import SEO from "@landingpage/core/src/components/SEO";
import Footer from "@landingpage/core/src/components/Footer";
import Topbar from "@landingpage/core/src/components/Topbar";

import Slices from "../components/Slices";

const Movie = ({ data }) => {
  const edges = get(data, "prismic.allMovies.edges", []);
  const page = get(edges, "0.node");
  if (!page) return null;

  return (
    <Layout>
      <SEO
        lang={page._meta.lang}
        title={RichText.asText(page.title)}
        description={RichText.asText(page.seo_description)}
        keywords={RichText.asText(page.seo_keywords)}
      />
      <Topbar topbar={page.topbar} logo={{ width: 140, height: 24 }} />
      <Slices slices={page.body} />
      <Footer
        footer={page.footer}
        publicationDate={page.lastPublicationDate}
        logo={{ width: 140, height: 24 }}
        borderTop
      />
    </Layout>
  );
};

export const query = graphql`
  query MovieById($uid: String!) {
    prismic {
      allMovies(uid: $uid) {
        edges {
          node {
            _meta {
              id
              lang
              lastPublicationDate
            }

            title

            seo_title
            seo_description
            seo_keywords

            topbar {
              ...TopbarFragment
            }
            footer {
              ...FooterFragment
            }

            body {
              ...MoviePageMovieSliderFragment
              ...MovieHeaderFragment
              ...MoviePageContentBlockFragment
            }
          }
        }
      }
    }
  }
`;

export default Movie;
